<template>
    <div class="classify_home">
        <!-- 左边 -->
        <div class="left">
            <div class="bady_left">
                <div class="wrapper" ref="left">
                    <div class="content">
                        <ClassifyLeft :fyleft = home.fyleft></ClassifyLeft>
                    </div>
                </div>
            </div>
        </div>

        <!-- 右边 -->
        <div class="right">
            <div class="bady_right">
                <div class="wrapper" ref="right">
                    <div class="content">
                        <ClassifyRight :rightClassify= home.rightClassify></ClassifyRight>
                        <!-- 图墙导航 -->
                        <Navigation :navigation = home.navigation> </Navigation>
                        <!-- 导航产品 -->
                        <ClassifyBottom :classifyBottom = home.classifyBottom></ClassifyBottom>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
import BScroll from "better-scroll";

import ClassifyLeft from "./classify/ClassifyLeft";
import ClassifyRight from "./classify/ClassifyRight";
import Navigation from "./classify/Navigation";
import ClassifyBottom from './classify/ClassifyBottom';

export default {
    components: {
        ClassifyLeft,
        ClassifyRight,
        Navigation,
        ClassifyBottom
    },
    computed: {
        // 对象展开符号
        ...mapState(["home"])
        // "home.warpImgs":function() {
        //     return this.$store.state.home.warpImgs;
        // }
    },
    methods: {
        initScroll() {
            this.$nextTick(() => {
                this.scroll = new BScroll(this.$refs.left, {
                    click:true
                });
            });
        },
          initScrolls() {
            this.$nextTick(() => {
                this.scroll = new BScroll(this.$refs.right, {
                    click:true
                });
            });
        },
        ...mapActions(["loadHomeData", "loadClassify", "loadNavigation"])
    },
    mounted() {
        this.loadHomeData();
        this.loadClassify();
        this.loadNavigation();
        this.initScroll();
        this.initScrolls();
    }
};
</script>



<style lang="less">
.wrapper {
    width: 100%;
    height: 100%;
    }
.bady_left {
    position: absolute;
    top: 100px; /*px*/
    bottom: 100px; /*px*/
    left: 0px;/*px*/
    }
.bady_right {
    position: absolute;
    top: 100px; /*px*/
    bottom: 100px; /*px*/
    left: 200px;/*px*/
    right: 0px;/*px*/
    }

</style>




