<template>
    <div class="app">
        <div>
            <!-- 搜索 -->
            <div>
                <Searchtwo></Searchtwo>
            </div>
            <!-- 导航 -->
            <div>
                <AppNav :appNav = home.appNav></AppNav>
            </div>
        </div>
        <!-- 滚轮 -->
        <div class="app_g">
            <div class="wrapper" ref="app_g">
                <div class="content">
                    <!-- 产品 -->
                    <!-- <div v-for="(v,i) of 40" :key="i">1</div> -->
                    <AppointBottom :AppBottom = home.AppBottom></AppointBottom>
                </div>
            </div>
        </div>
    </div>
</template>



<script>
import BScroll from "better-scroll";
import {mapState,mapActions} from "vuex";

import Searchtwo from "./appoint/Searchtwo";
import AppNav from './appoint/AppNav';
import AppointBottom from './appoint/AppointBottom';

export default {
    components: {
        Searchtwo,
        AppNav,
        AppointBottom
    },
    computed:{
        // 对象展开符号
        ...mapState(['home']),
        // "home.warpImgs":function() {
        //     return this.$store.state.home.warpImgs;
        // }
    },
    methods: {
        initScroll() {
            this.$nextTick(() => {
                this.scroll = new BScroll(this.$refs.app_g, {});
            });
        },
        ...mapActions(["loadAppNav","loadAppBottom"]),
        
    },
    mounted() {
        this.initScroll();
        this.loadAppNav();
        this.loadAppBottom();
    } 
};
</script>




<style lang="less">
.wrapper {
    width: 100%;
    height: 100%;
}
.app_g {
    position: absolute;
    top: 200px; /*px*/
    bottom: 0px; /*px*/
    left: 0px; /*px*/
    right: 0px;
}
</style>



