<template>
    <div>
        <div class="left" v-for="(v,i) of  rightClassify" :key="'rightClassify'+i" @click="change(v)">
            <!-- <router-link to="helloWorld" tag="button" -->
            <div class="boxs">
                <img :src="v.image" alt="" class="imgs">
                <div>{{ v.title }}</div>
            </div>
        </div >
    </div>
</template>
<script>
export default {
    props: {
        rightClassify: null
    },
    methods: {
        change(v){
            let str = v.link.match(/fcid.[0-9]*/)[0];
            this.$router.push('/Appoint?'+str+"&title="+v.title);
        }
    },
    mounted() {}
};
</script>


<style lang="less">
.left {
    width: 100%;
    // border: 1px solid red;
    display: inline-block;
    width: 150px; /*px*/
    height: 210px; /*px*/
    margin-left: 25px; /*px*/
    margin-top: 50px; /*px*/
    // margin-right: 25px;
    .boxs {
        width: 150px; /*px*/
        text-align: center;
        font-size: 25px;/*px*/
    }
    .imgs {
        height: 150px; /*px*/
        width: 150px; /*px*/
    }
}
</style>





