<template>
    <div class="body">
        <div class="warp">
            <swiper :options="swiperOptions">
                <swiper-slide v-for="(v,i) of warps" :key="'warp'+i">
                    <img :src="v.image_800" alt="" srcset="">
                </swiper-slide>
                <div class="swiper-pagination"  slot="pagination"></div>
            </swiper>
        </div>
    </div>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper';

import jsonp from 'fetch-jsonp';

import api from '../../server/api';


export default {
    components: {
        swiper,
        swiperSlide,
        pagination : '.swiper-pagination',
    },
    data(){
      return{
          swiperOptions:{
                autoplay: 2000,
                loop: true,
                pagination: ".swiper-pagination",
                paginationClickable: true
          } ,
          params: {
                appPlat: "m",
                pids: "51822,106930,51833,51836"
            },
            warps: []
        };
    },
    methods:{
        
        loadDatas(){
             api.loadDatasByPids(this.params).then(data => {
                console.log("==>", data);

                this.warps = data.data[51822].list;
            });
        }
    },
    mounted() {
        this.loadDatas();
    },

  }

</script>
<style lang="less">
.warp{
    width: 100%;
    height: auto;
    img{
        width: 100%;
        height: auto;
    }

}

</style>




