<template>
    <div class="app_box">
        <div class="app_box1" :class="v.sort | isClick(key)" v-for="(v,i) of appNav" :key="'appNav'+i" @click="typeChange(v)">
            <!-- <div class="box1"   :class="v.sortKey | isClick(key)" v-for="(v,i) of navigation" :key="'navigation'+i" @click="areaChange(v)"> -->
            <div class="app_box2">{{ v.title }}</div>
        </div>
    </div>
</template>



<script>
import { mapActions } from "vuex";

export default {
    props: {
        appNav: null
    },
    data() {
        return {
            key: "pop"
        };
    },

    filters: {
        isClick(value, key) {
            return value == key ? "appBottom" : "";
        }
    },

    methods: {
        ...mapActions(["loadAppBottom"]),

        typeChange(p) {
            // console.log(p);
            this.key = p.sort;
            let params = this.$route.query;
            params.sort = p.sort;
            this.loadAppBottom(params);
        }
    }
};
</script>



<style lang="less">
.app_box {
    width: 100%;
    height: 70px; /*px*/
    text-align: center;
    padding-top: 30px; /*px*/
    // border-top: 1px solid #cccccc; /*px*/
    border-bottom: 1px solid #cccccc; /*px*/
    // margin-top: 50px; /*px*/
    div:nth-child(2),
    div:nth-child(3) {
        border-right: 1px solid #cccccc; /*px*/
        border-left: 1px solid #cccccc; /*px*/
    }
    .app_box1 {
        // margin-left: 5px;/*px*/
        display: inline-block;
        // background: yellow;
        width: 20%;
        font-size: 32px; /*px*/
    }
}
.appBottom {
    color: #fc5979;
}
</style>





