<template>
    <div class="HotSearchList">
        <div v-for="(v,i) of search" :key="'search'+i" :style="{color:v.color}" @click="searchChange(v)">{{ v.frontword }}</div>
    </div>
</template>
<script>

import { mapMutations } from "vuex";


export default {
    props:{
        search:null
    }, 
      methods: {
        ...mapMutations(['setSearchHistory']),
        searchChange(v){
            this.$router.push('/Appoint?'+"&title="+v.frontword);
            if(!localStorage.word){
                localStorage.word="";
            }
            localStorage.word+=v.frontword+",";
            let arr=localStorage.word.replace(/,$/,"").split(",");
            this.setSearchHistory(arr);
        }
    }
}
</script>
<style lang="less">
    .HotSearchList{
        width: 90%;
        height: auto;
        margin:  100px auto 0px;
        &>div{
            width: auto;
            height: auto;
            border: 1px solid #ccc;
            border-radius: 5px;
            margin-left: 10px;
            margin-right: 10px;
            padding-left: 20px;
            padding-right: 20px;
            display: inline-block;
            font-size: 28px;
            margin-bottom: 20px;
        }
    }
</style>



