<template>
  <!-- <div class="home"> -->
    <!-- 搜索 -->
    <!-- <div>
      <search></search>
    </div> -->
    <!-- 滚轮 -->
    <div class="bady">
      <div class="wrapper" ref="helloWorld">
        <div class="content">
          <!-- <div v-for="(v,i) of 60" :key="i">1</div> -->
          <!-- 轮播 -->
          <lunbo></lunbo>
          <!-- 导航 -->
          <daohang :types = home.types></daohang>
          <!-- 促销 -->
          <div  class="title">超实惠-促销直达</div>
          <cuxiao :bebefit=home.bebefit></cuxiao>
          <!-- 热门 -->
          <div  class=" title1">热门分类</div>
          <renmen :hottype=home.hottype></renmen>
          <!-- 喜欢 -->
           <div  class=" title2">猜你喜欢</div>
          <likes :likes=home.likes></likes>
        </div>
      </div>
    </div>
    <!-- 导航 -->
    <!-- <div> -->

    <!-- </div>
  </div> -->
</template>

<script>
import BScroll from 'better-scroll';

import search from './Search';
import lunbo from './gunlun/LunBo';
import daohang from './gunlun/DaoHang';
import cuxiao from './gunlun/CuXiao';
import renmen from './gunlun/RenMen';
import likes from './gunlun/Likes';

import {mapState,mapActions} from "vuex";

export default {
    components: {
    search,
    lunbo,
    daohang,
    cuxiao,
    renmen,
    likes 
  },
  computed:{
        // 对象展开符号
        ...mapState(['home']),
        // "home.warpImgs":function() {
        //     return this.$store.state.home.warpImgs;
        // }
    },
 methods:{
  
   initScroll(){
     this.$nextTick(() => {
       this.scroll = new BScroll(this.$refs.helloWorld,{})
     });
   },
   ...mapActions(["loadHomeData"]),
   ...mapActions(["loadLikes"])
 },
  mounted() {
    this.initScroll();
    this.loadHomeData();
    this.loadLikes();
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" >
// .home {
//     height: 100%;
// }
.wrapper{
  width: 100%;
  height: 100%;
}
.bady {
    position: absolute;
    top: 100px; /*px*/
    bottom: 100px; /*px*/
    left: 0px;
    right: 0px;
}
.title{
  margin-top: 40px;
  margin-bottom: 50px;
  width: 100%;
  text-align: center;
  background: url("../../static/图标/beiji.png") no-repeat;
  background-position: center;
  background-size: 60%;
}
.title1 , .title2{
  margin-top: 50px;
  margin-bottom: 50px;
  width: 100%;
  text-align: center;
  background: url("../../static/图标/beiji.png") no-repeat;
  background-position: center;
  background-size: 60%;
}
</style>



