import jsonpUtil  from '../util/jsonpUtil';


const mce = 'https://mce.mogucdn.com/jsonp/';
const mce1 = "https://list.mogujie.com/search";
const mce2 = "https://mce.mogujie.com/jsonp/";
const mce3 = "https://list.mogujie.com/search";
const mce4 = "https://list.mogujie.com/module/mget";




export default{
    loadDatasByPids(params){
        let url = mce + "multiget/3";
        return jsonpUtil.request(url,params);
    },
    loadDatasBy(params){
        let url = mce1;
        return jsonpUtil.request(url,params);
    },
    loadDatasByRight(params){
        let url= mce2 + "makeup/3";
        return jsonpUtil.request(url,params);
    },
    loadDatasByNav(params){
        let url = mce3;
        return jsonpUtil.request(url,params);
    },
    loadDataByPlatform(params){
        let url = mce4;
        return jsonpUtil.request(url,params);
    }
}


