<template>
    <div class="box">
        <div class="box1"   :class="v.sortKey | isClick(key)" v-for="(v,i) of navigation" :key="'navigation'+i" @click="areaChange(v)">
            <div class="box2">{{ v.title }}</div>
        </div>
    </div>
</template>



<script>
import { mapActions } from "vuex";

export default {
    data() {
        return {
            key: "pop"
        };
    },
    filters: {
        isClick(value, key) {
            return value == key ? "areaTitle" : "";
        }
    },
    methods: {
        ...mapActions(["loadNavigation"]),

        areaChange(p) {
            // console.log(p);
            this.key = p.sortKey;
            this.loadNavigation(p.sortKey);
        }
    },
    props: {
        navigation: null
    }
};
</script>



<style lang="less">
.box {
    width: 100%;
    height: 70px; /*px*/
    text-align: center;
    padding-top: 30px; /*px*/
    border-top: 1px solid #cccccc; /*px*/
    border-bottom: 1px solid #cccccc; /*px*/
    margin-top: 50px; /*px*/
    div:nth-child(2) {
        border-right: 1px solid #cccccc; /*px*/
        border-left: 1px solid #cccccc; /*px*/
    }
    .box1 {
        // margin-left: 5px;/*px*/
        display: inline-block;
        // background: yellow;
        width: 30%;
        font-size: 32px; /*px*/
    }
}
.areaTitle {
  
   
        color: #fc5979;
    
}
</style>





