<template>
    <div class="home_classify">
        <div class="left_f" :class="v.maitKey | isClick(key)" v-for="(v,i) of  fyleft" :key="'fyleft'+i" @click="leftChange(v)">
            <div class=" CF">
                    <span ><div>{{ v.title }}</div></span>
            </div>
        </div>
    </div>
</template>
<script>
import { mapActions } from "vuex";

export default {
    data(){
       return{
            key:"41888"
       }
    }, 
    filters:{
        isClick(value,key){
            return value==key?"smallTitle":"";
        }
    },
    props: {
        fyleft: null
    },
    methods: {
           ...mapActions(['loadClassify','loadNavigation']),


        leftChange(p) {
            this.key = p.maitKey;
            this.loadClassify(p.maitKey);

            this.loadNavigation({fcid:p.miniWallkey,sort:'pop'});

        }
    }
};
</script>


<style lang="less">
.left_f {
    // widows: 20%;
    div {
        width: 200px; /*px*/
        height: 100px; /*px*/
        font-size: 30px; /*px*/
        background: #f6f6f6;
        text-align: center;
        display: table-cell;
        vertical-align: middle;
    }
}
.smallTitle{
    color: #FC5979;
    .CF{
        background: #ffffff;
        // width: 100%;
        span {
            border-left: 10px solid #FC5979;/*px*/
            line-height: 20px;
           div {
               background: #ffffff;
           }
        }
    }
   
}
</style>





