import api from '../../server/api';

const state = {
    warpImgs:[],
    types:[],
    bebefit:[],
    hottype:[],
    likes:[],
    fyleft:[],
    rightClassify:[],
    navigation:[],
    classifyBottom:[],
    appNav:[],
    AppBottom:[],
    search:[],
    inputWord:[],
    searchHistory:[],
}

const getters = {


}

const actions = {
    loadHomeData({ commit }){
        api.loadDatasByPids({
            appPlat:"m",
            pids:"51822,106930,51833,51836,41789"
        }).then(({data})=>{
            commit("setWarpImgs",data[51822].list);
            commit("setTypes",data[106930].list);
            commit("setBebefit",data[51833].list);
            commit("setHottype",data[51836].list);
            commit("setFyleft",data[41789].list);
        })
    },
    loadLikes({ commit }){
        api.loadDatasBy({
            apid:"9750" ,
            sort:"pop" ,
            cKey:"46"
        }).then(({result})=>{
            commit("setLikes",result.wall.docs);
            
        })
    },
    loadAppNav({ commit }){
        api.loadDatasBy({
            // fcid="",
            // title= "",
            // cKey="16",
            // sort="pop",
            // page="1"
        }).then(({result})=>{
            commit("setAppNav",result.filter.list);
            
        })
    },
    loadAppBottom({ commit },params={fcid:"50243",title:"时尚套装",sort:"pop"}){
        api.loadDatasBy({
            fcid:params.fcid,
            title:params.title,
            cKey:"16",
            sort:params.sort?params.sort:"pop",
            page:"1"
        }).then(({result})=>{
            commit("setAppBottom",result.wall.docs);
            
        })
    },
    loadClassify({ commit },pid="41888"){
        api.loadDatasByRight({
            apid:"m",
            pid:pid
        }).then(({data})=>{
            // console.log(data);
            commit("setRightClassify",data.categoryNavigation.list);  
        })
    },
    loadNavigation({ commit },params={sort:"pop",fcid:"10062603"}){
        api.loadDatasByNav({
            fcid: params.fcid,
            page:1,
            sort: params.sort,
            cKey:"h5-cube"
        }).then(({ result }) =>{
            commit("setNavigation",result.sortFilter); 
            commit("setClassifyBottom",result.wall.docs);
        })
    },
    loadDataSearch({ commit }){
        api.loadDataByPlatform({
            platform:"H5",
            code:"hotWord,sketch" 
        }).then(({data})=>{
            commit ("setSearchHot",data.hotWord.data),
            commit ("setInputWord",data.sketch.data)
        })

    }
    // loadClassifyBottom({ commit },page = "1"){
    //     api.loadDatasByNav({
    //         fcid:"",
    //         page:page,
    //         sort:"",
    //         cKey:"h5-cube"
    //     }).then(({ result }) =>{
    //         commit("setClassifyBottom",result.wall.docs); 
    //     })
    // }
}

const mutations = {
    setWarpImgs(state,data){
        state.warpImgs = data;
    },
    setTypes(state,data){
        state.types = data;
    },
    setBebefit(state,data){
        state.bebefit = data;
    },
    setHottype(state,data){
        state.hottype = data;
    },
    setLikes(state,data){
        state.likes = data;
    },
    setFyleft(state,data){
        state.fyleft = data;
    },
    setRightClassify(state,data){
        state.rightClassify = data;
    },
    setNavigation(state,data){
        state.navigation = data;
    },
    setClassifyBottom(state,data){
        state.classifyBottom = data;
    },
    setAppNav(state,data){
        state.appNav = data;
    },
    setAppBottom(state,data){
        state.AppBottom = data;
    },
    setSearchHot(state,data){
        state.search = data;
    },
    setInputWord(state,data){
        state.inputWord = data;
    },
    setSearchHistory(state,data){
        state.searchHistory = data;
    },
}

export default {
    state,
    actions,
    getters,
    mutations,
    
}






