<template>
    <div class="Bannertype">
        <div class="bannerType" v-for="(v,i) of types" :key="'types'+i">
            <div class="bannerTypeP"><img :src="v.image" alt=""></div>
            <div class="bannerTypeD">{{ v.title }}</div>
        </div>
    </div>
</template>


 <script>
export default {
    props: {
        types: null
    }
};
</script>



<style lang="less">
.Bannertype {
    margin-top: 40px; /*px*/
    text-align: center;
    .bannerType {
        width: 140px; 
        height: 200px; 
        display: inline-block;
        margin-left: 2px; /*px*/
        margin-right: 2px; /*px*/
        padding-left:6px; /*px*/
        .bannerTypeP {
            width: 130px;
            height: 130px;
            border-radius: 50%;
            margin-bottom: 10px; /*px*/
            & > img {
                width: 100%;
                height: 100%;
            }
        }
        .bannerTypeD {
            
            width: 140px;
            height: 34px;
        }
    }
}
</style>
 


