<template>
    <div>
        <div v-for="(v,i) of classifyBottom" :key="'classifyBottom'+i" class="bottom_box">
            <div>
                <!-- 图片 -->
                <div>
                    <img :src="v.img" alt="" class="bottom_img">
                </div>
                <!-- 介绍 -->
                <div class="bottom_font">{{ v.title }}</div>
                <!-- 价格 -->
                <div class="bottom_bottom">
                    <div class="font1">￥{{ v.price }}</div>
                    <div class="font2">{{ v.sale }} <i class="iconfont icon-shoucang"></i></div>
                   
                </div>
            </div>
        </div>
    </div>
</template>



<script>
export default {
    props: {
        classifyBottom: null
    },
}
</script>


<style lang="less">
.bottom_box{
    margin-left: 2%;
    margin-top: 20px;/*px*/
    width: 47%;
    display: inline-block;
}
.bottom_img{
    width: 100%;
    height: 300px;/*px*/
}
.bottom_font{
    margin-top: 10px;/*px*/
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;

}
.font1{
    color: #FC5979;
    font-size: 32px;/*px*/
    float: left;
}
.font2{
    color: #999999;
    float: right;
}
.bottom_bottom{
    margin-top: 10px;/*px*/
}
</style>





