<template>
    <div class="all-likes">
        <div v-for="(v,i) of likes" :key="'likes'+i" class="likes">
            <div class="likes-img"><img :src="v.img"></div>
            <div class="likes-title">{{ v.title }}</div>
            <div class="likes-desc">
                <span class="likes-price">¥{{ v.price }}</span>
                <span class="likes-cfav">{{ v.cfav }}<i class="iconfont icon-shoucang"></i></span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        likes:null
    }
}
</script>

<style lang="less">
    .all-likes{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        margin-top: 20px;/*px*/
        .likes{
            width: 50%;
            height: 650px;/*px*/
            .likes-img{
                width: 100%;
                height: 500px;/*px*/
                text-align: center;
                border-radius: 40px 40px 0px 0px;/*px*/
                img{
                    width:90%;
                }
            }
            .likes-title{
                width:80%;
                color: #AEB9BE;
                text-align: center;
                margin: 10px auto;
                overflow: hidden;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
            }
            .likes-desc{
                width: 80%;
                margin: 10px auto;
                display: flex;
                justify-content: space-between;
                .likes-price{
                    font-size: 30px;/*px*/
                    font-weight: bold;
                }
                .likes-cfav{
                    margin-right: 0px;
                    color: #AEB9BE;
                    .icon-shoucang{
                        margin-left: 10px;/*px*/
                    }
                }
            }
        }
    }
</style>



