<template>
    <div class="SearchBox">
        <div class="sousuo">
            <div class="jiantou" onclick="javascript:history.go(-1);">
                <i class="iconfont icon-xiangzuojiantou"></i>
            </div>
            <div class="search-box"><input type="text"></div>
            <div class="search-btn">搜索</div>
        </div>
        <hr>
        <!-- 历史搜索 -->
        <div class="history-search">
            <div class="history-serh">
                <i class="iconfont icon-sousuo"></i>历史搜索</div>
            <div class="clear">
                <i class="iconfont icon-lajixiang"></i>
            </div>
        </div>
        <!-- 存放历史记录 -->
        <div class="historylist">
            <div></div>
        </div>
        <hr>
        <!-- 热门搜索 -->
        <div class="HotSearch">
            <i class="iconfont icon-shoucang"></i>热门搜索</div>
        <!-- 搜索关键字 -->
     
            <SearchCategories :search = home.search></SearchCategories>
     <!--  -->
    </div>
</template>
<script>
import SearchCategories from "./SearchCategories";
import { mapState, mapActions } from "vuex";

export default {
    components: {
        SearchCategories
    },
    computed: {
        ...mapState(["home"])
    },
    methods: {
        ...mapActions(["loadDataSearch"]),
        ChangeCom(p) {}
    },
    mounted() {
        this.loadDataSearch();
    }
};
</script>
<style lang="less">
.SearchBox {
    width: 100%;
    height: 100%;
}
hr {
    color: #ccc;
}
.sousuo {
    width: 100%;
    height: 80px;
    & > div {
        height: 80px;
        display: inline-block;
    }
    .jiantou {
        width: 6%;
        height: 100%;
        i {
            font-size: 40px;
            line-height: 80px;
            text-align: center;
        }
    }
    .search-box {
        width: 75%;
        height: 80px;
        // vertical-align: middle;
        border-radius: 5px;

        & > input[type="text"] {
            box-sizing: border-box;
            text-align: left;
            outline: 0;
            text-decoration: none;
            width: 100%;
            height: 60px;
            margin-top: 10px;
            -web-kit-appearance: none;
            -moz-appearance: none;
            border-radius: 5px;
            border: 2px solid #ccc;
            font-size: 32px;
        }
        & > input[type="text"]:focus {
            border: 2px solid #f67592;
        }
    }
    .search-btn {
        width: 10%;
        line-height: 80px;
        font-size: 30px;
    }
}
.history-search {
    width: 100%;
    height: 80px;
    margin-top: 20px;
    font-size: 26px;
    .history-serh {
        float: left;
        margin-left: 30px;
    }
    .clear {
        float: right;
        margin-right: 30px;
    }
}
.historylist {
    width: 100%;
    height: auto;
    & > div {
        // border: 1px solid #ccc;
        width: auto;
        height: auto;
        font-size: 24px;
        padding-left: 5px;
        padding-right: 5px;
    }
}
.HotSearch {
    margin-top: 10px;
    float: left;
    margin-left: 30px;
    font-size: 26px;
}
</style>


