<template>
    <div>
        <div v-for="(v,i) of AppBottom" :key="'AppBottom'+i" class="app_bottom_box">
            <div>
                <!-- 图片 -->
                <div>
                    <img :src="v.img" alt="" class="app_bottom_img">
                </div>
                <!-- 介绍 -->
                <div class="app_bottom_font">{{ v.title }}</div>
                <!-- 价格 -->
                <div class="app_bottom_bottom">
                    <div class="app_font1">￥{{ v.price }}</div>
                    <div class="app_font2">{{ v.cfav }} <i class="iconfont icon-shoucang"></i></div>
                   
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        AppBottom: null
    },
}
</script>
<style lang="less">
.app_bottom_box{
    margin-left: 2%;
    margin-top: 20px;/*px*/
    width: 47%;
    display: inline-block;
}
.app_bottom_img{
    width: 100%;
    height: 300px;/*px*/
}
.app_bottom_font{
    margin-top: 10px;/*px*/
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;

}
.app_font1{
    color: #FC5979;
    font-size: 32px;/*px*/
    float: left;
}
.app_font2{
    color: #999999;
    float: right;
}
.app_bottom_bottom{
    margin-top: 10px;/*px*/
}
</style>





